body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.no-padding {
	padding: 0px;
}

.outline-card {
	filter: unset;
	border: 1px solid #ccc;
}

.split-view {
	display: grid;
	grid-template-columns: 50% 50%;
}

.titlebar-drag {
	position: absolute;
	top: 0px;
	left: 180px;
	width: 90vw;
	height: 40px;
	-webkit-user-select: none;
  	-webkit-app-region: drag;
}

.h2>div, .h2 {
	font-size: 40px;
}

.h3>div, .h3 {
	font-size: 35px;
}

.h4>div, .h4 {
	font-size: 30px;
}
.h5>div, .h5 {
	font-size: 25px;
}
.h6>div, .h6 {
	font-size: 20px;
	font-weight: 600;
	color:#272727;
}

.indent {
	text-indent: 15px;
}

#test-card>.card-content {
	padding: 30px;
}
.half-n-half {
	padding: 0px;
	margin: 0px;
	height: calc(100% - 60px);
	width: calc(100%);
	top: 60px;
	scrollbar-gutter: 8px;
	display: grid;
	grid-template-columns: 50% 50%;
}
.table-container {
	height: 100%;
	min-width: 100%;
	max-width: 100%;
}

/*Quote Table*/
.quote-table-card {
	min-width: 93.5% !important;
	max-width: 93.5% !important;
	margin-left: 3%;
	min-height: 83vh;
	max-height: 83vh;
	filter: unset;
	border: 1px solid #ccc;
}
.quote-table-card>.card-content {
	display: flex;
	flex-direction: column;
	padding: 0;

}
.quote-table-card>.card-content>.tablerow.header {
	background-color: #062a49;
	color:#fff;
	border-top-right-radius: 7.5px;
	border-top-left-radius: 7.5px;
	padding: 0;
	margin: 0;
	display: grid;
	grid-template-columns: 16% 12% 18% 18% 16% 16%;
}
.quote-table-card>.card-content>.tablerow.header>div {
	padding: 0;
	margin: 0;
	line-height: 40px;
	font-size: 20px;
	background-color: transparent;
}
.quote-table-card>.card-content {
	min-height: calc(100% - 40px);
	max-height: calc(100% - 40px);
	display: contents;
}

.quote-table-card>.card-content>.card-content.data-table {
	height: calc(83vh - 40px);
	overflow-y: auto !important;
	padding: 0;
}
.quote-table-card>.card-content>.card-content.data-table>:first-child {
	border-top: 1px solid #ccc;
}
.quote-table-card>.card-content>.card-content.data-table>.tablerow {
	height: fit-content;
	background-color: transparent;
	display: grid;
	grid-template-columns: 16% 12% 18% 18% 16% 16%;
	border-bottom: 1px solid #ccc;
}
.quote-table-card>.card-content>.card-content.data-table>.tablerow>.row-item {
	margin: 0px;
	padding: 0px;
}
.quote-table-card>.card-content>.card-content.data-table>:last-child {
	border-bottom: 0px;
}
.quote-table-card>.card-content>.card-content.data-table>.tablerow>div {
	width: max-content;
	justify-self: center;
}

.quote-table-card>.card-content>.card-content.data-table>.tablerow:hover {
	background-color: #ccc;
	border-bottom: 2px solid #43bc90;
}
.quote-table-card>.card-content>.card-content.data-table>.tablerow>:nth-child(5) {
	border-bottom: 2px solid #43bc90;
	margin-top: 5px;
}
.quote-table-card>.card-content>.card-content.data-table>:first-child:hover {
	border-bottom: 2px solid #bc4343;
}
.quote-table-card>.card-content>.card-content.data-table>:first-child>:nth-child(5) {
	border-bottom: 2px solid #bc4343;
}
.quote-table-card>.card-content>.card-content.data-table>:nth-child(5):hover {
	border-bottom: 2px solid #bc8743;
}
.quote-table-card>.card-content>.card-content.data-table>:nth-child(5)>:nth-child(5) {
	border-bottom: 2px solid #bc8743;
}

.table-button {
	width: 85px;
}

/*Quote Scheduler*/
#quote-scheduler-card {
	max-height: min-content;
	min-height: min-content;
	width: 30vw;
}
#quote-scheduler-card>.card-content {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 20px;
	padding: 20px;
}
#quote-scheduler-card>.card-content>.form-info-item {
	display: grid;
	grid-template-columns: 40% 60%;
	width: 100%;
	align-self: center;
	justify-content: center;
	font-size: 30px;
}
#quote-scheduler-card>.card-content>.form-info-item>input {
	width: 100%;
	height: 100%;
}