.job-log-cont {
	height: calc(100% - 100px);
	top: 100px;
}
.job-log-cont>.action-row {
	align-items: center;
	margin-left: 25px;
}
.job-log-cont>.action-row>.header>div {
	font-size: 40px;
	color: #272727;
	font-weight: 550;
}
.job-log-cont>.table-container {
	padding: 10px;
}
#log-filter-card {
	z-index: 60 !important;
	width: fit-content;
	height: 4vh;
	min-height: 5vh;
	max-height: 5vh;
}
#log-filter-card>.card-content {
	height: 100%;
}

.filter-form-item {
	align-items: center;
	justify-content: center;
	gap: 20px;
	font-size: large;
}
.filter-form-item>.form-info-title {
	font-weight: 600;
}

.log-action-button {
	height: 100% !important;
	width: 150px;
	line-height: 25px;
	background-color: #2f83c7;
}
.log-action-button:hover {
	background-color: #266ca5;
}

.joblog-table {
	height: calc(100% - 40px) !important;
	overflow-y: auto;
}

.job-log-card>.card-content {
	padding: 0px;
	height: 100%;
}
.job-log-card {
	height: 100%;
	min-width: 100%;
	overflow: hidden;
}
.job-log-card>.card-content>.tablerow.header {
	background-color: #062a49;
	color:#fff;
	border-top-right-radius: 7.5px;
	border-top-left-radius: 7.5px;
	padding: 0;
	margin: 0;
	display: grid;
	grid-template-columns: 16% 16% 16% 16% 16% 20%;
}

.job-log-card>.card-content>.tablerow.header>div {
	padding: 0;
	margin: 0;
	line-height: 40px;
	font-size: 20px;
	background-color: transparent;
	font-weight: 600;
}

.joblog-table>.tablerow {
	height: fit-content;
	display: grid;
	grid-template-columns: 16% 16% 16% 16% 16% 20%;
	align-items: center;
	border-bottom: 1px solid #ccc;
}
.joblog-table>.tablerow:hover {
	background-color: #f9f9f9;
}
.joblog-table>:last-child {
	border-bottom: 0px;
}
.joblog-table>.tablerow>.row-item {
	font-size: 20px;
}