#job-manager-card {
	height: 80vh;
	width: 70vw;
}

#job-manager-card>.card-content {
	height: calc(100% - 40px);
	display: flex;
	flex-direction: column;
}

.job-info {
}

.tabs-card {
	height: 100%;
	padding: 0px;
	overflow: hidden;
	border-radius: 10px;
	border: 1px solid #ccc;
	background-color: #fafafa;
}

.tabs-card>.menu-bar {
	filter: unset;
	border-bottom: 1px solid #ccc !important;
	height: 40px;
	display: flex;
	width: 100%;
}
.tabs-card>.menu-bar>.menu-button {
	border-radius: 0px;
	height: 40px;
	line-height: 40px !important;
	border-bottom: 1px solid #ccc !important;
}
.tabs-card>.menu-bar>:last-child {
	border-right: 0px;
}
/*Job Info*/
.tabs-card>.menu-bar>:nth-child(1) {
	background-color: #4172bb;
}
.tabs-card>.menu-bar>:nth-child(1):hover,
.tabs-card>.menu-bar>:nth-child(1).selected {
	background-color: #2a518b;
}
/*Funding*/
.tabs-card>.menu-bar>:nth-child(2) {
	background-color: #309255;
}
.tabs-card>.menu-bar>:nth-child(2):hover,
.tabs-card>.menu-bar>:nth-child(2).selected {
	background-color: #1f643a;
}
/*Site Info*/
.tabs-card>.menu-bar>:nth-child(3) {
	background-color: #bb4160;
}
.tabs-card>.menu-bar>:nth-child(3):hover,
.tabs-card>.menu-bar>:nth-child(3).selected {
	background-color: #922e47;
}
/*.tabs-card>.menu-bar>.menu-button.selected {
	background-color: #ccc;
	font-weight: 700;
}*/

.job-info-consultant {
	font-size: larger;
	font-weight: 550;
	color: #313131;
}
.job-info-progress {
	display: flex;
	gap: 10px;
}
.job-info-progress {
	font-size: large;
}
.job-info-progress>.progress {
	font-style: italic;
	font-weight: 550;
}

.job-manager-form-cont {
	display: grid;
	flex-direction: column;
	width: 100%;
	height: calc(100% - 40px);
	grid-template-columns: 50% 50%;
	row-gap: 10px;
	gap: 10px;
	overflow-y: auto;
	overflow-x: hidden;
}
.job-manager-form-items-cont>.form-info-item {
	padding: 10px;
	padding-left: 20px;
	padding-right: 20px;
	display: grid;
	grid-template-columns: 40% 60%;
	align-items: center;
	justify-content: center;
}
.job-manager-form-items-cont>.form-info-item>:nth-child(2) {
	width: 100%;
}
.job-manager-form-items-cont>.form-info-item>input {
	padding: 10px;
}
.job-manager-form-items-cont>.form-info-item>.form-info-title {
	font-size: larger;
	font-weight: 550;
	color: #212121;
	font-style: italic;
}

.form-item-header {
	grid-template-columns: 100% 0% !important;
}

.form-item-header>.form-info-title {
	font-size: xx-large !important;
	color: #000 !important;
	font-weight: 900;
	width: 100%;
	font-style: normal !important;
}