#job-creator-form {
	width: 50vw;
	overflow: hidden;
}

#job-creator-form>.card-content {
	padding: 0px;
	height: calc(100% - 40px);
}

.creator-quote-info-form-item {
	display: grid;
	grid-template-columns: 30% 70%;
	width: 100%;
	font-size: larger;
	gap: 0px;
}
.creator-quote-info-form-item>.form-info-title {
	font-weight: 600;
	color: #313131;
}
.creator-quote-info-form-item>:nth-child(2) {
	font-style: italic;
}

#quote-contract-info {
	min-height: 30%;
	max-height: 30%;
}

#created-jobs-form {
	min-height: 60%;
	max-height: 60%;
	border-bottom: 1px solid #ccc;
	border-top: 1px solid #ccc;
	background-color: #fcfcfc;
	overflow-y: auto;
}

#job-create-form-actions {
	min-height: 10%;
	max-height: 10%;
	align-items: center;
	padding: 10px;
}

#quote-info-form {
	padding: 10px;
	row-gap: 5px;
	display: flex;
	flex-direction: column;
}

#quote-contract-form {
	background-color: #f0f0f0;
	margin: 10px;
	border-radius: 10px;
	border: 1px solid #ccc;
	min-height: 80%;
	max-height: 80%;
	overflow-y: hidden;
}

.creator-header {
	padding: 0px;
	text-indent: 0px;
}

#quote-contract-form>.list-cont {
	text-align: left;
	overflow-y: scroll;
	height: 85%;
}
#quote-contract-form>.list-cont>.numbered-list {
	display: flex;
	flex-direction: column;
	row-gap: 10px;
}

.job-tablerow {
	height: fit-content;
	border-bottom: 1px solid #ccc;
}
.job-tablerow:hover {
	background-color: #f0f0f0
}